import React, {Component} from "react";
import { connect } from "react-redux";
import { OraInput } from "eshop/components/OraCommonComponents";

class OraRadioButton extends Component {

    handleChange(event) {
        this.props.onChange(event);
    }

    render() {
        return (
            <label className="o-radio opl-radio">
                <OraInput type="radio" name={this.props.name}
                    checked={this.props.selectedValue === this.props.value}
                    value={this.props.value}
                    onChange={this.props.onValueChange.bind(this)}
                    />
                <span className="o-ci"></span>
                <span className="o-ci-label">{this.props.text}</span>
            </label>
        )
    }
}

